/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React from 'react';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import { useStaticQuery, graphql } from 'gatsby';
import PhotoFull from '../../images/photo_full.jpg';

const SEO = ({ metaTags }) => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
          }
        }
      }
    `
  )

  const {
    title = 'Hieu Pham goes to dot com world',
    description = 'Welcome to my homepage',
    lang = 'en',
    meta = [],
    thumbnail,
    pageUrl
  } = metaTags;
  const metaDescription = description || site.siteMetadata.description;
  const metaThumbnail = thumbnail ? `https://www.hieugoesto.com${thumbnail}` : 'https://www.hieugoesto.com/images/market/banner.png';
  const metaUrl = pageUrl || 'https://www.hieugoesto.com';

  const googleMetaTags = [
    {
      name: 'description',
      content: metaDescription,
    },
    {
      name: 'keywords',
      content: 'homepage,portfolio,profile',
    },
    {
      name: 'author',
      content: site.siteMetadata.author,
    }
  ]

  const facebookMetaTags = [
    {
      property: 'og:title',
      content: title,
    },
    {
      property: 'og:description',
      content: metaDescription,
    },
    {
      property: 'og:type',
      content: 'website',
    },
    {
      property: 'og:image',
      content: metaThumbnail,
    },
    {
      property: 'og:url',
      content: metaUrl,
    },
    {
      property: 'fb:app_id',
      content: '272354566715786',
    }
  ]

  const twitterMetaTags = [
    {
      name: 'twitter:card',
      content: 'summary',
    },
    {
      name: 'twitter:creator',
      content: site.siteMetadata.author,
    },
    {
      name: 'twitter:title',
      content: title,
    },
    {
      name: 'twitter:description',
      content: metaDescription,
    },
    {
      name: 'twitter:image',
      content: metaThumbnail,
    }
  ]

  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      bodyAttributes={{
        class: '',
      }}
      title={title}
      titleTemplate={`%s | ${site.siteMetadata.title}`}
      meta={[
        ...googleMetaTags,
        ...facebookMetaTags,
        ...twitterMetaTags,
      ].concat(meta)}
      link={
        [{
          rel: 'canonical',
          key: pageUrl,
          href: pageUrl
        }]
      }
    >
      <link rel="prefetch" href={ PhotoFull } />
    </Helmet>
  )
}

SEO.defaultProps = {
  metaTags: {},
}

SEO.propTypes = {
  metaTags: PropTypes.object
}

export default SEO;
